import React, { ReactNode } from 'react'
import styled, { StyledComponent } from 'styled-components'

interface IGridMenuProps {
  children: ReactNode
  columns?: number | string
  rows?: number
  height?: string
  maxWidth?: string
}

const GridElement: StyledComponent<
  'div',
  any,
  IGridMenuProps,
  never
> = styled.div`
  height: ${(props: IGridMenuProps) => props.height || 'auto'};
  width: 100%;
  max-width: ${(props: IGridMenuProps) => props.maxWidth || 'auto'};
  overflow: ${(props: IGridMenuProps) =>
    props.height === '100vh' ? 'hidden' : 'initial'};
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: ${(props: IGridMenuProps) =>
    typeof props.columns === 'number'
      ? `repeat(${props.columns}, 1fr)`
      : props.columns};
  grid-template-rows: repeat(
    ${(props: IGridMenuProps) => props.rows || 1},
    1fr
  );
  margin: 0 auto;

  @media screen and (max-width: 40rem) {
    grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
  }
`

export const Grid = ({
  children,
  columns,
  rows,
  height,
  maxWidth,
}: IGridMenuProps) => (
  <GridElement
    columns={columns}
    rows={rows}
    height={height}
    maxWidth={maxWidth}
  >
    {children}
  </GridElement>
)
