import styled from 'styled-components'
import { Link } from 'gatsby'
import { theme } from '../data/theme'

export const StyledLink = styled(Link)`
  text-decoration: none;
  color: ${theme.primary};

  & :hover {
    color: ${theme.primary_hover};
  }
`

export const StyledA = styled.a`
  text-decoration: none;
  color: ${theme.primary};

  & :hover {
    color: ${theme.primary_hover};
  }
`
